const Base = require('../base.js');
//获取用户列表
module.exports = class extends Base {
    async indexAction() {
        let model = this.model('spxq');
        let data = ""

        if (this.ctx.post('id')) {
            data = await model.where({
                id: this.ctx.post('id')
            }).find();
           data.spt= data.spt.split(",")
        } else {
            data = await model.page(this.ctx.post('page')).order('id DESC').where({
                name: ['like', `%${this.ctx.post('name')||""}%`]
            }).countSelect();
        }
 
        return this.fail(data); //2输出json 推荐

    }

};
